.login{
height: 100vh;
background-color: rgb(141, 176, 216);
display: flex;
align-items: center;
justify-content: center;


    .card{
        width: 50%;
        display: flex;
        background-color: white;
        border-radius: 10px;
        min-height: 600px;
        overflow: hidden;

        .left{
            flex: 1;
            background: linear-gradient(rgba(20, 57, 104, 0.494), rgba(20, 57, 104, 0.494)) ,url("https://images.pexels.com/photos/10727250/pexels-photo-10727250.jpeg")
            center;
            background-size: cover;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 30px;
            color: white;

            h1{
                font-size: 100px;
                line-height: 100px;
            }
            p{
                font-weight: bold;
            }
            span{font-size: 14px;}
            button{
                width: 50%;
                padding: 10px;
                border: none;
                background-color: white;
                color: navy;
                font-weight: bold;
                cursor: pointer;
            }
        }
        .right{
            flex: 1;
            padding: 50px;
            display: flex;
            flex-direction: column;
            gap: 50px;
            justify-content: center;
            h1{
                color: #555;
            }
            form{
                display: flex;
                flex-direction: column;
                gap: 30px;
                input{
                    border: none;
                    border-bottom: 1px solid lightgray;
                    padding: 20px 10px;
                }
                button{
                    width: 50%;
                    padding: 10px;
                    border: none;
                    background-color: rgb(37, 75, 141);
                    color: white;
                    font-weight: bold;
                    cursor: pointer;
                }
            }
        }
    }
}